<template>
  <transition name="fade-down">
    <div v-if="showCharacter" class="character">
      <div class="character__details">
        <div class="character__face-pictures">
          <div v-for="facepic in facepics"
               :key="facepic" class="character__face-picture"
               :class="{'character__face-picture--selected': selectedFace === facepic}"
               @click="setState(facepic)"
          >
            <img :src="require(`@/assets/img/${folderName}/${facepic}.png`)" />
          </div>
        </div>
        <div class="character__information">
          <div class="character__name"> {{ name[state] }} </div>
          <div class="character__description"> {{ description[state] }} </div>
        </div>
      </div>
      <div class="character__moves">
      <transition-group tag="div" name="fade" class="character__move__selection">
        <div v-for="move in moveset"
           :key="move.name"
           class="character__move"
           :class="{'character__move--selected': selectedMove === move}"
           @click="setSelectedMove(move)"
        >
          <img :src="require(`@/assets/img/${folderName}/moves/${state}/${move.name.toLowerCase().replace(/[: ]+/g, '_')}.png`)" />
        </div>
      </transition-group>

      <div class="character-display__backline"></div>

      <div class="character__move__display">
          <div v-if="selectedMove.alternatives" class="character__move__display__alt-moves">
            <div :key="selectedMove.name">
              <div class="character-display__backline"></div>
              <div class="character__move"
                   :class="{'character__move--selected': selectedAltMove === selectedMove}"
                   @click="setAlternativeMove(selectedMove)"
              >
                <img :src="require(`@/assets/img/${folderName}/moves/${state}/${selectedMove.name.toLowerCase().replace(/ /g, '_')}.png`)" />
              </div>
              <div v-for="move in selectedMove.alternatives"
                   :key="move.name"
                   class="character__move"
                   :class="{'character__move--selected': selectedAltMove === move}"
                   @click="setAlternativeMove(move)"
              >
                <img :src="require(`@/assets/img/${folderName}/moves/${state}/${move.name.toLowerCase().replace(/ /g, '_')}.png`)" />
              </div>
            </div>
          </div>
          <transition-group tag="div" class="character__move__display__information" name="slide-fade">
            <div class="character__move__top-bar" :key="selectedMoveName">
              <div class="move__name"> {{ selectedMoveName }} </div>
              <div class="move__information">
                <div class="move__pill move__cost">Chakra: {{ selectedMoveCost }} </div>
                <div class="move__pill move__cooldown">Cooldown: {{ selectedMoveCooldown }} </div>
              </div>
            </div>
            <div class="move__description" :key="selectedMoveDescription" v-html="selectedMoveDescription"></div>
          </transition-group>
      </div>
    </div>
    </div>
  </transition>
</template>
<script>
  export default {
    data () {
      return {
        facepics: [],
        folderName: '',
        name: {},
        description: {},
        moves: {},
        state: 'base',
        moveset: [],
        selectedFace: '',
        selectedMove: [],
        selectedAltMove: {}
      }
    },
    computed: {
      showCharacter () {
        return this.folderName !== ''
      },
      selectedMoveName () {
        return (this.selectedAltMove ? this.selectedAltMove.name : this.selectedMove.name)
      },
      selectedMoveCost () {
        return (this.selectedAltMove ? this.selectedAltMove.cost : this.selectedMove.cost)
      },
      selectedMoveCooldown () {
        return (this.selectedAltMove ? this.selectedAltMove.cooldown : this.selectedMove.cooldown)
      },
      selectedMoveDescription () {
        return (this.selectedAltMove ? this.selectedAltMove.description : this.selectedMove.description)
      }
    },
    methods: {
      setup (character) {
        if (Object.keys(character).length === 0) return this.reset()
        this.facepics = character.facepics
        this.folderName = character.folderName
        this.name = character.name
        this.description = character.description
        this.moves = character.moves
        this.setState('base')
      },
      reset () {
        this.facepics = []
        this.folderName = ''
        this.name = {}
        this.description = {}
        this.moves = {}
        this.state = 'base'
        this.moveset = []
        this.selectedFace = ''
        this.selectedMove = []
        this.selectedAltMove = {}
      },
      setState (state) {
        this.state = state
        this.setSelectedFace()
        this.setMoveSet()
        this.setSelectedMove(this.moveset[0])
      },
      setSelectedFace () {
        this.selectedFace = this.state
      },
      setMoveSet () {
        this.moveset = this.moves[this.state]
      },
      setSelectedMove (move) {
        this.selectedMove = move
        this.setAlternativeMove(false)
        if (move.alternatives) {
          this.setAlternativeMove(this.selectedMove)
        }
      },
      setAlternativeMove (move) {
        this.selectedAltMove = move
      }
    },
    mounted () {
      // this.setState(this.facepics[0])
    }
  }
</script>
