<template>
  <div class="character-display">
    <div class="character-display__backline"></div>
    <div v-for="character in characters" :key="character.folderName"
         class="character-full"
         :class="[`character-full--${character.folderName}`, {'character-full--selected': isSelected(character)}]"
         @click="selectCharacter(character)"
         @mouseover="setHover(character)"
         @mouseout="mouseOut()"
    >
      <span class="character-full__border">
        <img class="character-full__image" :src="require(`@/assets/img/${character.folderName}/large.png`)" />
      </span>
    </div>
  </div>
</template>
<script>
  import Characters from '../characters'
  export default {
    props: {
      selectedCharacter: {
        required: false,
        type: Object
      }
    },
    data () {
      return {
        characters: Characters,
        hoveredCharacter: false
      }
    },
    methods: {
      selectCharacter (character) {
        if (this.selectedCharacter === character) {
          return this.$emit('characterSelected', {})
        }
        this.$emit('characterSelected', character)
      },
      setHover (character) {
        this.hoveredCharacter = character
      },
      mouseOut () {
        this.hoveredCharacter = false
      },
      isSelected (character) {
        return (this.hoveredCharacter === character) || (!this.hoveredCharacter && this.selectedCharacter === character)
      }
    }
  }
</script>
