<template>
  <div id="app">
    <div class="site-title">
      <h1 class="site__header">Shinobi Arena</h1>
      <small class="site__tagline">Fight. Survive. Thrive.</small>
    </div>
    <div class="site-content">
      <character-picker
              :selectedCharacter="selectedCharacter"
              @characterSelected="characterSelected" />
      <character ref="character" />
      <div class="character-display__backline" style="position: relative; width: 100%; left: 0; margin-top: 40px;"></div>
      <discord />
    </div>
  </div>
</template>

<script>
import Discord from '@/components/Discord'
import CharacterPicker from '@/components/CharacterPicker'
import Character from '@/components/Character'
export default {
  name: 'app',
  data () {
    return {
      selectedCharacter: {}
    }
  },
  methods: {
    characterSelected (character){
      this.selectedCharacter = character
      if (this.$refs.character) this.$refs.character.setup(character)
    }
  },
  components: {
    Discord,
    CharacterPicker,
    Character
  }
}
</script>

<style src="@/assets/scss/main.scss" lang="scss" />
